import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@views/Home'
import QueenAunt from '@views/QueenAunt'
import QueenBaseInfo from '@views/QueenBaseInfo'
import QueenLipstick from '@views/QueenLipstick'
import QueenFood from '@views/QueenFood'
import QueenPhoto from '@views/QueenPhoto'
import Login from '@views/Login'
import QueenGift from '@views/QueenGift'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'login',
    component: Login,
    meta: { title: '首页' },
    children: []
  },
  {
    path: '/home',
    name: 'home',
    component: Home,
    meta: { title: '首页' },
    children: []
  },
  {
    path: '/baseInfo',
    name: 'baseInfo',
    component: QueenBaseInfo,
    meta: { title: '属性' },
    children: []
  },
  {
    path: '/food',
    name: 'food',
    component: QueenFood,
    meta: { title: '吃货' },
    children: []
  },
  {
    path: '/aunt',
    name: 'aunt',
    component: QueenAunt,
    meta: { title: '可怜' },
    children: []
  },
  {
    path: '/lipstick',
    name: 'lipstick',
    component: QueenLipstick,
    meta: { title: '欲罢不能' },
    children: []
  },
  {
    path: '/photo',
    name: 'photo',
    component: QueenPhoto,
    meta: { title: '秀' },
    children: []
  },{
    path: '/gift',
    name: 'gift',
    component: QueenGift,
    meta: { title: '礼物' },
    children: []
  },
  
]

const router = new VueRouter({
  routes
})

export default router
